.App {
  width: 100%;
  height: 100%;
}
.App .App-header {
  height: 100px;
  width: 70%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.App .App-header .App-logo {
  height: 70%;
  width: 250px;
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
}
.App .App-header .selectBox {
  left: 32%;
  top: 4%;
  width: 200px;
  height: 50px;
  z-index: 300;
  text-align: center;
  margin-right: 20%;
}
.App .App-header .selectBox p {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #71b406;
}
.App .App-header .selectBox p i {
  width: 15px;
  height: 10px;
  margin-top: 2px;
  margin-left: 5px;
  background-size: contain;
  background-repeat: no-repeat;
}
.App .App-header .selectBox ul {
  display: none;
}
.App .App-header .selectBox ul li {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 50px;
  background: #e7e7e7;
  list-style: none;
}
.App .App-header .selectBox ul li a {
  text-decoration: none;
  color: black;
}
.App .App-header .selectBox ul li a:hover {
  color: #71b406;
}
.App .App-header .selectBox:hover {
  background: #e7e7e7;
}
.App .App-header .selectBox:hover ul {
  display: block;
}
.App .App-header .selectBox:hover ul li {
  border-top: 2px solid white;
}
.App .App-header .App-gongGao {
  height: 80px;
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: red;
}
.App .App-content {
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
  background: #d7d7d7;
}
.App .App-content .App-banner-div {
  height: 64.5%;
  width: 100%;
  position: relative;
}
.App .App-content .App-banner-div .App-banner {
  height: 100%;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
}
.App .App-content .App-banner-div:after {
  position: absolute;
  content: '';
  width: 110%;
  height: 100%;
  top: 0;
  left: 0;
  box-shadow: -50px -50px 100px 10px #d7d7d7 inset;
}
.App .App-content .inner-content {
  background: white;
  height: 65%;
  width: 70%;
  margin: 0 auto;
  position: relative;
  top: -350px;
  border-radius: 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.App .App-content .inner-content .inner-content-child {
  height: 90.5%;
  width: 45%;
  font-size: 12px;
}
.App .App-content .inner-content .inner-content-child .child-images {
  height: 55.3%;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
}
.App .App-content .inner-content .inner-content-child .child-child2 {
  width: 100%;
  height: 14%;
  display: flex;
}
.App .App-content .inner-content .inner-content-child .child-child2 .child-child2-qh {
  padding-left: 2%;
  box-sizing: border-box;
  width: 35.2%;
  height: 100%;
  background: #3c52ed;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App .App-content .inner-content .inner-content-child .child-child2 .child-child2-time {
  width: 65%;
  height: 100%;
  background: #9bb4ff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App .App-content .inner-content .inner-content-child .child-child2 .child-child2-time :nth-child(2) {
  width: 10%;
}
.App .App-content .inner-content .inner-content-child .child-child2 .child-child2-time :nth-child(3) {
  width: 10%;
}
.App .App-content .inner-content .inner-content-child .child-child2 .child-child2-time :nth-child(4) {
  width: 10%;
}
.App .App-content .inner-content .inner-content-child .child-child3 {
  width: 100%;
  height: 32%;
  display: flex;
}
.App .App-content .inner-content .inner-content-child .child-child3 .child-child3-font {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35.3%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
}
.App .App-content .inner-content .inner-content-child .child-child3 .child-child3-number {
  background: #8ce8ff;
  width: 65%;
  height: 65%;
  display: flex;
  justify-content: center;
}
.App .App-content .inner-content .inner-content-child .child-child3 .child-child3-number ul {
  list-style: none;
  display: flex;
  width: 90%;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-bottom: 5%;
}
.App .App-content .inner-content .inner-content-child .child-child3 .child-child3-number ul li {
  width: 19.5%;
  height: 70%;
  background-size: contain;
  background-repeat: no-repeat;
  align-self: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 20px;
  text-shadow: 1px 2px 0px black;
}
.App .App-content .inner-content .inner-content-child .child-child3 .child-child3-number ul li span {
  margin-right: 2px;
  margin-bottom: 2px;
}
.App .App-footer {
  width: 70%;
  height: 150px;
  margin: 0 auto;
}
.App .App-footer div:nth-child(1) {
  width: 70%;
  height: 40%;
  margin: 0 auto;
  display: flex;
}
.App .App-footer div:nth-child(1) p {
  width: 25%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
}
.App .App-footer div:nth-child(2) {
  width: 100%;
  height: 50%;
  margin: 0 auto;
  display: flex;
}
.App .App-footer div:nth-child(2) p:nth-child(1) {
  width: 25%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
}
.App .App-footer div:nth-child(2) p:nth-child(2) {
  padding-top: 1%;
  box-sizing: border-box;
  width: 70%;
  height: 100%;
  margin-left: 5%;
  background-size: contain;
  background-repeat: no-repeat;
}
